.container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }
  .container .spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -16px 0 0 -16px;
    width: 32px;
    height: 32px;
    animation: spinner 1s steps(12) infinite; }

@keyframes spinner {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
