.notification-li + .notification-li {
  border-top: 1px solid #ddd; }

.notification-li a {
  position: relative;
  padding: 12px 66px 13px 16px;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #000; }

.notification-li .notification-profile-image {
  padding-right: 10px; }
  .notification-li .notification-profile-image img {
    width: 34px;
    height: 34px;
    border-radius: 100%;
    overflow: hidden; }

.notification-li .notification-username .notification-date {
  margin-left: 5px;
  color: #999; }

.notification-li .notification-username .notification-comment {
  font-weight: 300;
  margin-left: 5px; }

.notification-li .notification-image {
  position: absolute;
  top: 50%;
  right: 16px;
  margin-top: -20px; }
  .notification-li .notification-image img {
    width: 40px;
    height: 40px; }
