.navigation {
  width: 100%;
  border: 1px solid #e6e6e6;
  background-color: white;
  display: flex;
  justify-content: center; }
  .navigation .inner {
    max-width: 935px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 77px;
    padding: 25px 0; }
    .navigation .inner .column {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 33%; }
      .navigation .inner .column:first-child {
        justify-content: flex-start; }
      .navigation .inner .column:last-child {
        justify-content: flex-end; }
      .navigation .inner .column .logo {
        max-width: 125px; }
      .navigation .inner .column .search-input {
        width: 215px;
        box-sizing: border-box;
        padding: 5px;
        border: solid 1px #dbdbdb;
        border-radius: 3px;
        background-color: #fafafa;
        font-size: 14px;
        font-weight: 300; }
        .navigation .inner .column .search-input::placeholder {
          color: #999; }
        .navigation .inner .column .search-input:focus {
          outline: none;
          background-color: white; }
      .navigation .inner .column .nav-icon {
        margin-left: 30px;
        position: relative;
        cursor: pointer; }
