.profile {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 935px;
  margin: 0 auto;
  align-items: center; }
  .profile .profile-contents .profile-container {
    display: flex;
    margin-bottom: 44px; }
    .profile .profile-contents .profile-container .profile-image {
      width: 292px;
      text-align: center; }
      .profile .profile-contents .profile-container .profile-image img {
        width: 152px;
        height: 152px;
        border-radius: 100%;
        border: 1px solid #cccccc;
        overflow: hidden; }
    .profile .profile-contents .profile-container .profile-info .profile-top {
      margin-bottom: 20px; }
      .profile .profile-contents .profile-container .profile-info .profile-top .username {
        font-size: 28px;
        color: #262626;
        font-weight: 300; }
      .profile .profile-contents .profile-container .profile-info .profile-top .user-profile-edit {
        margin-left: 20px;
        padding: 5px 9px;
        border: 1px solid #dbdbdb;
        border-radius: 4px;
        background-color: transparent;
        text-decoration: none;
        font-size: 14px;
        font-weight: 700;
        color: #262626; }
    .profile .profile-contents .profile-container .profile-info .profile-btm {
      display: flex; }
      .profile .profile-contents .profile-container .profile-info .profile-btm li {
        font-size: 16px; }
        .profile .profile-contents .profile-container .profile-info .profile-btm li + li {
          margin-left: 40px; }
        .profile .profile-contents .profile-container .profile-info .profile-btm li.cursor {
          cursor: pointer; }
  .profile .profile-image-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
