.formBox .form {
  color: #262626;
  font-size: 16px;
  font-weight: 600; }
  .formBox .form .form-row {
    margin-bottom: 15px;
    display: flex;
    align-items: center; }
    .formBox .form .form-row .form-dt {
      padding-right: 25px;
      width: 25%;
      text-align: right;
      box-sizing: border-box; }
      .formBox .form .form-row .form-dt img {
        padding: 0;
        width: 38px;
        height: 38px;
        border-radius: 100%;
        overflow: hidden;
        border: 1px solid #e6e6e6;
        background-color: transparent; }
    .formBox .form .form-row .form-dd {
      width: 75%; }
      .formBox .form .form-row .form-dd .username {
        font-weight: 300;
        font-size: 20px;
        line-height: 22px;
        margin-bottom: 2px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap; }
      .formBox .form .form-row .form-dd .image-change-text {
        border: 0;
        color: #3897f0;
        display: inline;
        padding: 0;
        font-weight: 600;
        font-size: 14px;
        line-height: 18px; }
  .formBox .form .form-input {
    padding: 0 10px;
    width: 355px;
    height: 32px;
    border-radius: 3px;
    border: 1px solid #efefef;
    box-sizing: border-box;
    background-color: #fff; }
  .formBox .form .form-select {
    width: 150px;
    height: 32px;
    border-radius: 3px;
    border: 1px solid #efefef;
    box-sizing: border-box;
    background-color: #fff;
    color: #262626;
    font-size: 16px; }
  .formBox .form .form-submit {
    margin-left: 25%;
    padding: 5px 9px;
    max-height: 30px;
    background-color: #3897f0;
    border: 1px solid #3897f0;
    border-radius: 4px;
    font-size: 14px;
    color: #fff; }
    .formBox .form .form-submit[disabled] {
      opacity: 0.3; }
    .formBox .form .form-submit div {
      position: static; }
      .formBox .form .form-submit div img {
        position: static;
        margin: 0;
        width: 20px;
        height: 20px; }
  .formBox .form button {
    outline: none;
    cursor: pointer; }

.formBox .modal {
  position: relative; }
  .formBox .modal .modal-mask {
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 20; }
  .formBox .modal .modal-contents {
    position: relative;
    z-index: 21;
    width: 400px;
    height: 222px;
    border-radius: 10px;
    background-color: #fff;
    overflow: hidden;
    text-align: center; }
    .formBox .modal .modal-contents .modal-title-area {
      padding: 32px; }
      .formBox .modal .modal-contents .modal-title-area .modal-title {
        font-size: 18px;
        font-weight: 600;
        color: #262626; }
    .formBox .modal .modal-contents .modal-menu-list .modal-menu-li button {
      border: none;
      border-top: 1px solid #efefef;
      cursor: pointer;
      line-height: 1.5;
      margin: 0;
      min-height: 48px;
      padding: 4px 8px;
      width: 100%;
      background-color: transparent;
      display: block;
      font-size: 14px;
      outline: none; }
    .formBox .modal .modal-contents .modal-menu-list .modal-menu-li .modal-menu-button-type1 {
      color: #3897f0;
      font-weight: 700; }
    .formBox .modal .modal-contents .modal-menu-list .modal-menu-li .modal-menu-button-type2 {
      color: #ed4956;
      font-weight: 700; }
    .formBox .modal .modal-contents .modal-menu-list .modal-menu-li .modal-menu-button-type3 {
      color: inherit; }

.complete-modal {
  position: fixed;
  left: 50%;
  bottom: 0;
  margin-left: -200px;
  width: 400px;
  height: 50px;
  line-height: 50px;
  background: #000;
  text-align: center;
  color: #fff; }
