.comment-box {
  margin-top: 10px;
  padding: 15px 0;
  border-top: 1px solid #efefef; }
  @media (min-width: 320px) and (max-width: 450px) {
    .comment-box {
      display: none; } }
  .comment-box .input {
    border: 0;
    height: 20px !important;
    resize: none;
    width: 100%;
    font-size: 14px; }
    .comment-box .input:focus {
      outline: none; }
    .comment-box .input::placeholder {
      color: #999; }
