.feed-photo {
  border: 1px solid #e6e6e6;
  background-color: white;
  border-radius: 3px;
  width: 100%;
  max-width: 600px;
  margin-bottom: 60px; }
  @media (min-width: 320px) and (max-width: 450px) {
    .feed-photo {
      background-color: transparent;
      border: 0;
      margin-bottom: 30px; } }
  .feed-photo:last-child {
    margin-bottom: 0; }
  .feed-photo .header {
    padding: 15px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e6e6e6; }
    .feed-photo .header .image {
      width: 30px;
      height: 30px;
      border-radius: 100%;
      margin-right: 15px; }
    .feed-photo .header .header-column {
      display: flex;
      flex-direction: column; }
      .feed-photo .header .header-column .creator {
        font-weight: 600;
        margin-bottom: 5px; }
        .feed-photo .header .header-column .creator a {
          font-weight: 600;
          text-decoration: none;
          font-size: inherit;
          color: inherit; }
      .feed-photo .header .header-column .location {
        font-size: 12px; }
  .feed-photo.feed-photo-row {
    position: relative;
    border: none;
    max-width: 815px; }
    .feed-photo.feed-photo-row .content-box {
      display: flex;
      align-items: flex-start; }
      .feed-photo.feed-photo-row .content-box .feed-image {
        width: 60%;
        min-height: 435px;
        background-color: #000;
        display: flex;
        align-items: center; }
        .feed-photo.feed-photo-row .content-box .feed-image img {
          width: 100%;
          vertical-align: top; }
      .feed-photo.feed-photo-row .content-box .meta {
        padding-bottom: 63px;
        width: 40%;
        min-height: 435px; }
        .feed-photo.feed-photo-row .content-box .meta .header {
          padding: 5px 0 15px; }
        .feed-photo.feed-photo-row .content-box .meta .caption {
          margin-top: 15px; }
        .feed-photo.feed-photo-row .content-box .meta .comment-input {
          position: absolute;
          right: 0;
          bottom: 0;
          padding: 0 15px;
          width: 40%;
          box-sizing: border-box; }
      .feed-photo.feed-photo-row .content-box .comment-box {
        max-height: 244px;
        overflow: hidden;
        overflow-y: auto; }
  .feed-photo .meta {
    padding: 15px;
    padding-bottom: 0;
    padding-top: 10px;
    background-color: #fff; }
    .feed-photo .meta .time-stamp {
      font-size: 10px;
      color: #999; }
    .feed-photo .meta .caption {
      font-weight: 300; }
      .feed-photo .meta .caption strong {
        font-weight: 700; }
  .feed-photo .comment-box {
    margin: 15px 0 10px; }
